:root{
    --PrimaryColor: hsl(199, 100%, 33%);
    --SecondaryColor: hsl(187, 85%, 43%);
    --gradientColor: linear-gradient(to right, hsl(187, 85%, 43%),hsl(199, 100%, 33%));
    --whiteColor: hsl(0, 0%, 100%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam: hsl(0, 0%, 93%);
    --greyText: rgb(190, 190, 190);
    --inputColor: rgb(239, 239, 239);
}

.home{
    width: 100%;
    // height: 90vh;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    
    .overlay{
        position: absolute;
        height: 100%;
        width: 100%;
        background: rgb(47, 106, 127);
        mix-blend-mode: hard-light;
        top: 0;
        bottom: 0;
        z-index: 1;
    }

    video{
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        bottom: 0;
        object-fit: cover;
    }

    .homeContent{
        width: 100%;
        height: max-content;
        padding: 8rem 1.5rem 3rem;
        display: flex;
        row-gap: 3rem;
        flex-direction: column;
        align-items: stretch;
        justify-content: center;
        margin: auto;
        color: var(--whiteColor);
        z-index: 100;

       .textDiv{
        padding: 2rem e0;
        .smallText{
            font-size: 13px;
            text-transform: uppercase;
        } 

        .homeTitle{
            position: relative;
            font-size: 1.8rem;
            // width: max-content; will ve active at wider screen
            
            // &::after{
            //     position: absolute;
            //     content: '';
            //     background: var(--SecondaryColor);
            //     height: 5px;
            //     width: 115px;
            //    right: 0px;
            //    bottom: 5px;
            //    z-index: -1;
            // }
        }
       }

       .cardDiv{
            position: relative;
            padding: 2rem 1rem;
            background: var(--whiteColor);
            border-radius: 10px;
            gap: 1rem;

            label{
                display: block;
                color: var(--textColor);
                padding-bottom: 10px;
                font-size: 13px;
            }

            .input{
                height: 40px;
                padding: 0 1rem;
                background: var(--inputColor);
                border-radius: 3rem;
            
                input{
                    background: none;
                    border: none;
                    outline: none;
                    width: 100%;
                }

                .icon{
                    font-size: 18px;
                    color: var(--PrimaryColor) !important;
                    margin-left: 10px;
                }


                input[type=range]{
                    background: blue;
                }   //CHECK THIS LATER
            }

            .searchOptions{
                position: absolute;
                background: var(--gradientColor);
                width: max-content;
                bottom: -.7rem;
                column-gap: .8rem;
                padding: 5px 1rem;
                border-radius: 3rem;
                left: 50%;
                transform: translate(-50%);
                justify-content: center;
                &:hover{
                    background: var(--PrimaryColor);
                }

                .icon{
                    font-size: 18px;
                }

                span{
                    font-size: 13px;

                }
            }


        }

        .homeFooterIcons{
            width: 100%;
            justify-content: space-between;
            .icon{
                font-size: 18px;
                margin: 0 5px;
                color: var(--whiteColor);
                font-weight: 600;
                &:hover{
                    color: var(--PrimaryColor);
                }

            }
        }
    }
}
