:root{
    --PrimaryColor: hsl(199, 100%, 33%);
    --SecondaryColor: hsl(187, 85%, 43%);
    --gradientColor: linear-gradient(to right, hsl(187, 85%, 43%),hsl(199, 100%, 33%));
    --whiteColor: hsl(0, 0%, 100%);
    --blackColor: hsl(201, 33%, 16%);
    --textColor: hsl(240, 4%, 36%);
    --whiteColorDeam: hsl(0, 0%, 93%);
    --greyText: rgb(190, 190, 190);
    --inputColor: rgb(239, 239, 239);
}

.footer{
    position: relative;
    padding: 2rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
     margin: auto;
    .videoDiv{
        position: absolute;
        height: 100%;
        video{
          height: 100%;
          object-fit: cover;
        }

        &::after{
            content: '';
            position: absolute;
            background: rgba(10, 84, 107, 0.307);
            height: 100%;
            width: 100%;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            mix-blend-mode: multiply;
        }
    }

    .secContent{
        width: 100%;
        height: max-content;
        padding: 2rem initial;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin: auto;
        row-gap: 2rem;
        z-index: 100;

        .contactDiv{
            justify-content: space-between;
            flex-direction: column;
            gap: 1rem;

            .text{
                small{
                    font-size: 13px;
                    font-weight: 400;
                    color: var(--whiteColor);
                }

                h2{
                    color: var(--whiteColor);
                    font-size: 1.8rem;
                }
            }

            .inputDiv{
                width: 100%;
                gap: 1rem;
                flex-direction: column;
                input{
                    width: 100%;
                  padding: .5rem .6rem;
                  border-radius: 3rem;
                  border: 1px solid var(--whiteColor);
                  outline: none;
                  background: transparent;
                  color: var(--whiteColor);
                  &::placeholder{
                    color: var(--whiteColor);
                    opacity: .5;
                  }
                   
                }

                .btn{
                    width: 100%; //at a wider screen, width will be max-content.
                    justify-content: center;
                    color: var(--whiteColor);
                    font-weight: 500;
                    gap: .5rem;
                    .icon{
                        font-size: 18px;
                    }
                }
            }
        }

        .footerCard{
          position: relative;
          padding: 1rem 1rem 4rem 1rem;
          gap: 2rem;
          border-radius: 1rem;
          background: var(--whiteColor);
          width: 100%; //chech width at a widder screen.
          justify-content: center;
          flex-direction: column;
          align-items: flex-start;
          overflow: hidden;


          .footerIntro{
            justify-content: center;
            align-items: flex-start;
            flex-direction: column;
            row-gap: .5rem;

            .logoDiv{
                .logo{
                    color: var(--blackColor);
                    font-weight: 600; 
                    
                    .icon{
                        font-size: 25px;
                        color: var(--PrimaryColor);
                        transform: translateY(5px);
                       }
                }
            }

            .footerParagraph{
                font-size: 13px;
                color: var(--textColor); 
            }

            .footerSocials{
                column-gap: .5rem;
                .icon{
                    color: var(--blackColor);
                    font-size: 20px;
                    &:hover{
                        color: var(--PrimaryColor);              
                    }
                }
            }
          }

          .footerLinks{
              gap: 1rem;

              .linkGroup{
                .groupTitle{
                    display: block;
                    font-weight: 600;
                    color: var(--blackColor);
                    margin-bottom: .5rem;
                }

                .footerList{
                    font-size: 15px;
                    color: var(--textColor);
                    transition: .3s ease-in-out;
                    .icon{
                        font-size: 15px;
                        color: var(--greyText);
                    }

                    &:hover{
                        color: var(--PrimaryColor);
                        transform: translateX(7px);
                        .icon{
                            color: var(--SecondaryColor);
                        }
                    }


                }
              }
          }
           
          .footerDiv{
            flex-direction: column;
            position: absolute;
            bottom:0;
            left: 0;
            right: 0;
            background: var(--SecondaryColor);
            padding: .5rem 2rem;

            small{
                font-size: 10px;
                color: var(--whiteColor);
            }
          }

        }

    }
}